<template>
	<view class="wrapper padding">
		<view class="search">
			<input 
				type="text" 
				placeholder="请输入客户姓名" 
				v-model="name" 
				confirm-type="search" 
				@confirm="search"
				:focus="Focus"
				>
			<image src="../../static/clear.png" @click="clear"></image>
		</view>
		<view class="histroy" v-if="historyList.length>0">
			<view class="title">搜索历史</view>
			<view class="list">
				<view class="item" v-for="(item,index) in historyList" :key="index" @click="historySearch(item)">
					{{item}}
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				historyList:[],
				name:'',
				Focus:true
			};
		},
		methods:{
			clear(){
				this.name='';
				this.Focus=true
			},
			historySearch(item){
				this.name=item
				this.search()
			},
			search(){
				if(!this.name){ uni.showToast({title: '请输入客户姓名',icon:'none'});this.Focus=true;return}
				this.perfectNavigateTo({
					url:"/pages/staff/searchResult?name="+this.name
				})
				// todo
				if(this.historyList.indexOf(this.name)<0){ //没有
					this.historyList.unshift(this.name)
				}
				if(this.historyList.length>=7){
					this.historyList.pop()
				}
				uni.setStorageSync('historyList', this.historyList);
			}
		},
		onShow(){
			//todo储存
			let _list=uni.getStorageSync('historyList');
			if(_list.length>0){
				this.historyList=_list
			}else{
				this.historyList=[]
			}
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		}
	}
</script>

<style lang="stylus">
	.wrapper
		.search
			display:flex
			flex-direction row
			align-items center
			height:112upx
			border-bottom 2upx solid #E3E9F0
			input
				flex:1
				font-size 32upx
			image
				width:48upx
				padding:10upx
				height:48upx
		.title
			margin-top:58upx
			font-size:40upx;
			font-family:PingFangSC-Semibold,PingFang SC;
			font-weight:600;
			color:rgba(30,30,30,1);
		.list
			display:flex
			flex-direction row
			margin-top:33upx
			flex-wrap wrap
			.item
				height:72upx;
				padding:0upx 20upx
				line-height 72upx
				background:rgba(243,246,251,1);
				border-radius:8upx;
				text-align center
				margin-right:20upx
				margin-top:10upx
</style>
